import { useEffect, useState } from "react";
import styled from "styled-components/macro";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation, withTranslation } from "react-i18next";
import _ from "lodash";

import Breadcrumb from "antd/lib/breadcrumb";
import Table from "antd/lib/table";
import Button from "antd/lib/button";
import Tooltip from "antd/lib/tooltip";
import Typography from "antd/lib/typography";

import EmptyDynamicButton from "assets/images/dynamicButton/empty-dynamic-button.webp";
import Edit from "assets/icons/common/edit.svg";
import Delete from "assets/icons/common/delete.svg";
import Details from "assets/icons/users/details.svg";

import { setShowModalConfirmDelete } from "redux/slices/global";

import {
  loadListExposeAPII,
  deleteExposeAPII,
  getDetailsExposeAPII,
  setShowModalSetting,
  getDetailsPayload,
} from "redux/slices/exposeAPIInteraction";

import ModalConfirmDelete from "components/Modal/ModalConfirmDelete";
import ModalSetting from "pages/ExposeAPIInteraction/ModalSetting";
import ModalPayload from "pages/ExposeAPIInteraction/ModalPayload";
import { changeTitlePage } from "redux/slices/authenticated";

function ExposeAPIInteraction(props) {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { Column } = Table;
  const { Text } = Typography;

  useEffect(() => {
    dispatch(changeTitlePage("Expose API Interaction"));
    //eslint-disable-next-line
  }, [t]);

  const { userRuleGlobal } = useSelector((state) => state.userReducer);

  const { listExposeAPI } = useSelector(
    (state) => state.exposeAPIInteractionReducer
  );

  const [dataTable, setDataTable] = useState([]);
  const [dataDelete, setDataDelete] = useState({});

  const checkRule = (rule) => {
    if (
      userRuleGlobal.find(
        (item) =>
          _.get(item, "domain", "") === "dynamic_button" &&
          item.actions.includes(rule)
      )
    )
      return true;
    else return false;
  };

  const _onEdit = (id) => {
    return () => {
      dispatch(getDetailsExposeAPII({ expose_api_id: id }));
    };
  };

  const _onShowPayload = (id) => {
    return () => {
      dispatch(getDetailsPayload({ expose_api_id: id }));
    };
  };

  const _onDelete = (id) => {
    dispatch(setShowModalConfirmDelete(true));
    setDataDelete({ expose_api_id: id });
  };

  useEffect(() => {
    dispatch(loadListExposeAPII());
  }, [dispatch]);

  useEffect(() => {
    let tempList = [];
    listExposeAPI.map((item) => {
      return tempList.push({
        key: item._id,
        type: item.expose_api_type,
        create_date: item.created_date,
        interaction_type: item.interaction_type,
        description: item.description,
      });
    });

    setTimeout(() => {
      setDataTable(tempList);
    }, 50);
  }, [dispatch, listExposeAPI]);

  return (
    <Wrapper>
      <WrapBreadcrumb>
        <Breadcrumb>
          <Breadcrumb.Item onClick={() => navigate("/settings")}>
            {t("settings.settings")}
          </Breadcrumb.Item>
          <BreadcrumbItem>Expose API Interaction</BreadcrumbItem>
        </Breadcrumb>
        {listExposeAPI.length > 0 && checkRule("create") && (
          <AddButton onClick={() => dispatch(setShowModalSetting(true))}>
            + {t("exposeAPIInteraction.add")}
          </AddButton>
        )}
      </WrapBreadcrumb>

      {listExposeAPI.length === 0 ? (
        <Empty>
          <img src={EmptyDynamicButton} alt="empty" />
          <p>
            {t("object.noObject")} <span>Expose API Interaction</span>
          </p>
          {checkRule("create") && (
            <AddButton onClick={() => dispatch(setShowModalSetting(true))}>
              + {t("exposeAPIInteraction.add")}
            </AddButton>
          )}
        </Empty>
      ) : (
        <WrapWorkflows>
          <Table
            pagination={false}
            dataSource={dataTable}
            scroll={{ x: "max-content" }}
          >
            <Column
              title={t("coreSetting.type")}
              dataIndex="type"
              key="type"
              render={(text) => (
                <span style={{ textTransform: "capitalize" }}>{text}</span>
              )}
            />
            <Column
              title={t("common.description")}
              dataIndex="description"
              key="description"
              render={(text) => (
                <Text style={{ width: "300px" }} ellipsis={{ tooltip: text }}>
                  {text}
                </Text>
              )}
            />
            <Column
              title={t("exposeAPIInteraction.interactionType")}
              dataIndex="interaction_type"
              key="interaction_type"
              render={(text) => (
                <span style={{ textTransform: "capitalize" }}>{text}</span>
              )}
            />
            <Column
              title={t("common.createdDate")}
              dataIndex="create_date"
              key="create_date"
              width="200px"
            />

            {(checkRule("edit") || checkRule("delete")) && (
              <Column
                title={t("common.action")}
                dataIndex="action"
                key="action"
                fixed="right"
                width="150px"
                render={(text, record) => (
                  <WrapAction>
                    <Tooltip title="Show payload">
                      <img
                        onClick={_onShowPayload(record.key)}
                        src={Details}
                        alt="details"
                      />
                    </Tooltip>
                    {checkRule("edit") && (
                      <Tooltip title={t("common.edit")}>
                        <img
                          onClick={_onEdit(record.key)}
                          src={Edit}
                          alt="edit"
                        />
                      </Tooltip>
                    )}
                    {checkRule("delete") && (
                      <Tooltip title={t("common.delete")}>
                        <img
                          onClick={() => _onDelete(record.key)}
                          src={Delete}
                          alt="delete"
                        />
                      </Tooltip>
                    )}
                  </WrapAction>
                )}
              />
            )}
          </Table>
        </WrapWorkflows>
      )}

      <ModalSetting />
      <ModalPayload />

      <ModalConfirmDelete
        title={""}
        decs={t("common.descriptionDelete")}
        methodDelete={deleteExposeAPII}
        dataDelete={dataDelete}
        isLoading={false}
      />
    </Wrapper>
  );
}

export default withTranslation()(ExposeAPIInteraction);

const Wrapper = styled.div`
  padding: 16px 24px;

  .ant-breadcrumb-link {
    cursor: pointer;
  }

  .ant-switch-checked {
    background-color: ${(props) => props.theme.main};
  }
`;

const WrapBreadcrumb = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const BreadcrumbItem = styled(Breadcrumb.Item)`
  font-family: var(--roboto-500);
  font-size: 18px !important;
  color: #2c2c2c;
  cursor: default;
`;

const Empty = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 24px;
  font-size: 16px;

  p {
    color: #2c2c2c;
    margin-bottom: 16px;
  }

  span {
    color: ${(props) => props.theme.main};
  }

  img {
    margin-bottom: 8px;
    border-radius: 50%;
  }
`;

const AddButton = styled(Button)`
  background: ${(props) => props.theme.main};
  border: 1px solid ${(props) => props.theme.main};
  height: 40px;

  span {
    color: #fff;
    font-size: 16px;
  }

  :hover {
    background: ${(props) => props.theme.darker}!important;
    color: #fff !important;
  }

  :active {
    background: ${(props) => props.theme.main};
    color: #fff;
  }

  :focus {
    background: ${(props) => props.theme.main};
    color: #fff;
  }
`;

const WrapWorkflows = styled.div`
  background: #fff;
  padding: 24px;
  margin-top: 16px;

  .ant-table-thead > tr > th {
    font-size: 16px;
    color: #2c2c2c;
    background: #f0f0f0;
    border-right: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    padding: 8.5px 16px;

    :before {
      display: none;
    }

    :first-child {
      border-left: 1px solid #d9d9d9;
    }
  }

  .ant-table-tbody > tr > td {
    font-size: 16px;
    color: #2c2c2c;
    padding: 8.5px 16px;

    :first-child {
      border-left: 1px solid #d9d9d9;
    }

    :last-child {
      border-right: 1px solid #d9d9d9;
    }
  }

  .ant-table-content {
    ::-webkit-scrollbar {
      height: 8px;
    }
  }
`;

const WrapAction = styled.div`
  img {
    cursor: pointer;
    margin-left: 8px;

    :hover {
      background: #eeeeee;
    }
  }
`;
