import { useEffect, useState } from "react";
import styled from "styled-components/macro";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation, withTranslation } from "react-i18next";
import _ from "lodash";

import Modal from "antd/lib/modal";
import Form from "antd/lib/form";
import Button from "antd/lib/button";
import Input from "antd/lib/input";
import Checkbox from "antd/lib/checkbox";
import Select from "antd/lib/select";
import Tooltip from "antd/lib/tooltip";
import Row from "antd/lib/row";
import Col from "antd/lib/col";
import DeleteIcon from "assets/icons/common/delete.svg";

import {
  setShowModalSetting,
  createExposeAPII,
  updateExposeAPII,
  loadObjectFields,
  getDetailsExposeAPIIResult,
} from "redux/slices/exposeAPIInteraction";

const exposeAPIType = [
  {
    label: "Create",
    value: "create",
  },
  {
    label: "Update",
    value: "update",
  },
  {
    label: "Check",
    value: "check",
  },
];

const interactionTypes = [
  {
    label: "Call",
    value: "call",
  },
  {
    label: "Email",
    value: "email",
  },
  {
    label: "IC",
    value: "ic",
  },
];

const AuthenticationType = [
  {
    label: "Basic",
    value: "basic",
  },
  {
    label: "Token",
    value: "token",
  },
];

function ModalSetting() {
  const [form] = Form.useForm();
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const [typeAuthen, setTypeAuthen] = useState("");
  const [interactionType, setInteractionType] = useState("");
  const [interactionKeys, setInteractionKeys] = useState([]);
  const [interactionDetailsKeys, setInteractionDetailsKeys] = useState([]);
  const [createNewRecord, setCreateNewRecord] = useState({
    contact: false,
    ticket: false,
    lead: false,
  });
  const [mappingContact, setMappingContact] = useState({
    requiredFields: [],
    optionalFields: [],
  });
  const [mappingTicket, setMappingTicket] = useState({
    requiredFields: [],
    optionalFields: [],
  });
  const [mappingLead, setMappingLead] = useState({
    requiredFields: [],
    optionalFields: [],
  });

  const { showModalSetting, objectFields, detailsExposeAPI, allFields } =
    useSelector((state) => state.exposeAPIInteractionReducer);

  const _onSubmit = (values) => {
    let data = {
      expose_api_type: values.expose_api_type,
      interaction_type: values.interaction_type,
      authentication_type: typeAuthen,
      username: values.username,
      password: values.password,
      owner: values.owner,
      description: values.description,
      field_return: values.field_return,
      is_batch_action: values.is_batch_action,
      interaction_mapping: interactionKeys,
      interaction_detail_mapping: interactionDetailsKeys,
      object_contact_setting: {
        query_key: values.object_contact_setting_query_key,
        upsert: values.object_contact_setting_upsert,
        add_field: values.object_contact_setting_addField,
        match_with: values.object_contact_setting_match_with,
        requiredFields: mappingContact.requiredFields,
        optionalFields: mappingContact.optionalFields,
      },
      object_ticket_setting: {
        query_key: values.object_ticket_setting_query_key,
        upsert: values.object_ticket_setting_upsert,
        add_field: values.object_ticket_setting_addField,
        match_with: values.object_ticket_setting_match_with,
        requiredFields: mappingTicket.requiredFields,
        optionalFields: mappingTicket.optionalFields,
      },
      object_lead_setting: {
        query_key: values.object_lead_setting_query_key,
        upsert: values.object_lead_setting_upsert,
        add_field: values.object_lead_setting_addField,
        match_with: values.object_lead_setting_match_with,
        requiredFields: mappingLead.requiredFields,
        optionalFields: mappingLead.optionalFields,
      },
    };

    if (_.isEmpty(detailsExposeAPI)) {
      dispatch(
        createExposeAPII({
          ...data,
        })
      );
    } else {
      dispatch(
        updateExposeAPII({
          ...data,
          _id: detailsExposeAPI._id,
        })
      );
    }
  };

  const _onCancel = () => {
    dispatch(setShowModalSetting(false));
    dispatch(getDetailsExposeAPIIResult({}));
    form.resetFields();
    setTypeAuthen("");
    setInteractionType("");
    setInteractionKeys([]);
    setInteractionDetailsKeys([]);
    setCreateNewRecord({
      contact: false,
      ticket: false,
      lead: false,
    });
    setMappingContact({
      requiredFields: [],
      optionalFields: [],
    });
    setMappingTicket({
      requiredFields: [],
      optionalFields: [],
    });
    setMappingLead({
      requiredFields: [],
      optionalFields: [],
    });
  };

  const onSelectAuthenType = (e) => {
    setTypeAuthen(e);
  };

  const _onAddKeyInteraction = () => {
    setInteractionKeys([
      ...interactionKeys,
      {
        key: undefined,
        value: "",
      },
    ]);
  };

  const _onDeleteKeyInteraction = (idx) => {
    let temp = [...interactionKeys];
    temp.splice(idx, 1);
    setInteractionKeys(temp);
  };

  const handleKeyInteraction = (value, idx, type) => {
    let temp = _.cloneDeep(interactionKeys);
    temp[idx][type] = value;

    if (type === "key") {
      temp[idx].value = value.split("_").slice(1, -1).join("_");
    }
    setInteractionKeys(temp);
  };

  const _onAddKeyInteractionDetails = () => {
    setInteractionDetailsKeys([
      ...interactionDetailsKeys,
      {
        key: undefined,
        value: "",
      },
    ]);
  };

  const _onDeleteKeyInteractionDetails = (idx) => {
    let temp = [...interactionDetailsKeys];
    temp.splice(idx, 1);
    setInteractionDetailsKeys(temp);
  };

  const handleKeyInteractionDetails = (value, idx, type) => {
    let temp = _.cloneDeep(interactionDetailsKeys);
    temp[idx][type] = value;

    if (type === "key") {
      temp[idx].value = value.split("_").slice(1, -1).join("_");
    }
    setInteractionDetailsKeys(temp);
  };

  const normalizeData = (data) => {
    return _.map(data, (field) => ({
      key: field.value,
      label: field.label,
      value: field.value.split("_").slice(1, -1).join("_"),
      required: field.required,
      type: field.type,
    }));
  };

  const _onCreateNewRecord = (e, type) => {
    setCreateNewRecord({ ...createNewRecord, [type]: e.target.checked });

    let requiredFields = [];
    let temp = [];

    switch (type) {
      case "contact":
        requiredFields = _.get(objectFields, "obj_contact_00000001", []).filter(
          (field) => field.required && field.type !== "id"
        );
        temp = normalizeData(requiredFields);
        setMappingContact({
          requiredFields: [...temp],
          optionalFields: [],
        });
        form.setFieldValue("object_contact_setting_addField", undefined);

        break;
      case "ticket":
        requiredFields = _.get(objectFields, "obj_ticket_00000001", []).filter(
          (field) => field.required && field.type !== "id"
        );
        temp = normalizeData(requiredFields);
        setMappingTicket({
          requiredFields: [...temp],
          optionalFields: [],
        });
        form.setFieldValue("object_ticket_setting_addField", undefined);

        break;
      case "lead":
        requiredFields = _.get(objectFields, "obj_lead_00000001", []).filter(
          (field) => field.required && field.type !== "id"
        );
        temp = normalizeData(requiredFields);
        setMappingLead({
          requiredFields: [...temp],
          optionalFields: [],
        });
        form.setFieldValue("object_lead_setting_addField", undefined);

        break;

      default:
        break;
    }
  };

  const handleAddFieldsCreate = (value, type) => {
    let temp = [];

    switch (type) {
      case "contact":
        temp = normalizeData(value);
        setMappingContact({
          ...mappingContact,
          optionalFields: [...temp],
        });
        break;
      case "ticket":
        temp = normalizeData(value);
        setMappingTicket({
          ...mappingTicket,
          optionalFields: [...temp],
        });
        break;
      case "lead":
        temp = normalizeData(value);
        setMappingLead({
          ...mappingLead,
          optionalFields: [...temp],
        });
        break;

      default:
        break;
    }
  };

  const handleChangeFieldCreate = (value, idx, isRequired, type) => {
    let fields = [];

    switch (type) {
      case "contact":
        fields = isRequired
          ? _.cloneDeep(mappingContact.requiredFields)
          : _.cloneDeep(mappingContact.optionalFields);
        fields[idx].value = value;
        setMappingContact({
          ...mappingContact,
          [isRequired ? "requiredFields" : "optionalFields"]: [...fields],
        });
        break;
      case "ticket":
        fields = isRequired
          ? _.cloneDeep(mappingTicket.requiredFields)
          : _.cloneDeep(mappingTicket.optionalFields);
        fields[idx].value = value;
        setMappingTicket({
          ...mappingTicket,
          [isRequired ? "requiredFields" : "optionalFields"]: [...fields],
        });
        break;
      case "lead":
        fields = isRequired
          ? _.cloneDeep(mappingLead.requiredFields)
          : _.cloneDeep(mappingLead.optionalFields);
        fields[idx].value = value;
        setMappingLead({
          ...mappingLead,
          [isRequired ? "requiredFields" : "optionalFields"]: [...fields],
        });
        break;

      default:
        break;
    }
  };

  const loadObjectInteractionDetails = (value) => {
    let objectID = "";
    switch (value) {
      case "call":
        objectID = "obj_call_interaction_detail_00000001";
        break;
      case "email":
        objectID = "obj_email_interaction_detail_00000001";
        break;
      case "ic":
        objectID = "obj_ic_interaction_detail_00000001";
        break;

      default:
        break;
    }

    setInteractionDetailsKeys([]);
    setInteractionType(value);
    dispatch(
      loadObjectFields({
        api_version: "2",
        object_id: objectID,
      })
    );
  };

  useEffect(() => {
    if (!_.isEmpty(detailsExposeAPI)) {
      form.setFieldsValue({
        expose_api_type: detailsExposeAPI.expose_api_type,
        interaction_type: detailsExposeAPI.interaction_type,
        authenticationType: detailsExposeAPI.authentication_type,
        username: detailsExposeAPI.username,
        password: detailsExposeAPI.password,
        owner: detailsExposeAPI.owner,
        description: detailsExposeAPI.description,
        field_return: detailsExposeAPI.field_return,
        is_batch_action: detailsExposeAPI.is_batch_action,
        object_contact_setting_query_key:
          detailsExposeAPI.object_contact_setting.query_key,
        object_contact_setting_upsert:
          detailsExposeAPI.object_contact_setting.upsert,
        object_contact_setting_addField:
          detailsExposeAPI.object_contact_setting.add_field,
        object_contact_setting_match_with:
          detailsExposeAPI.object_contact_setting.match_with,
        object_ticket_setting_query_key:
          detailsExposeAPI.object_ticket_setting.query_key,
        object_ticket_setting_upsert:
          detailsExposeAPI.object_ticket_setting.upsert,
        object_ticket_setting_addField:
          detailsExposeAPI.object_ticket_setting.add_field,
        object_ticket_setting_match_with:
          detailsExposeAPI.object_ticket_setting.match_with,
        object_lead_setting_query_key:
          detailsExposeAPI.object_lead_setting.query_key,
        object_lead_setting_upsert: detailsExposeAPI.object_lead_setting.upsert,
        object_lead_setting_addField:
          detailsExposeAPI.object_lead_setting.add_field,
        object_lead_setting_match_with:
          detailsExposeAPI.object_lead_setting.match_with,
      });

      loadObjectInteractionDetails(detailsExposeAPI.interaction_type);

      setTypeAuthen(detailsExposeAPI.authentication_type);
      setInteractionType(detailsExposeAPI.interaction_type);
      setInteractionKeys(detailsExposeAPI.interaction_mapping);
      setInteractionDetailsKeys(detailsExposeAPI.interaction_detail_mapping);
      setMappingContact({
        requiredFields: detailsExposeAPI.object_contact_setting.requiredFields,
        optionalFields: detailsExposeAPI.object_contact_setting.optionalFields,
      });
      setMappingTicket({
        requiredFields: detailsExposeAPI.object_ticket_setting.requiredFields,
        optionalFields: detailsExposeAPI.object_ticket_setting.optionalFields,
      });
      setMappingLead({
        requiredFields: detailsExposeAPI.object_lead_setting.requiredFields,
        optionalFields: detailsExposeAPI.object_lead_setting.optionalFields,
      });

      setCreateNewRecord({
        contact: detailsExposeAPI.object_contact_setting.upsert,
        ticket: detailsExposeAPI.object_ticket_setting.upsert,
        lead: detailsExposeAPI.object_lead_setting.upsert,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [detailsExposeAPI, form]);

  useEffect(() => {
    dispatch(
      loadObjectFields({
        api_version: "2",
        object_id: "init",
      })
    );
  }, [dispatch]);

  return (
    <ModalCustom
      title={t("exposeAPIInteraction.settings")}
      visible={showModalSetting}
      footer={null}
      width={800}
      onCancel={_onCancel}
      afterClose={_onCancel}
    >
      <Form
        form={form}
        onFinish={_onSubmit}
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 16 }}
        colon={false}
        labelAlign="left"
      >
        <Form.Item
          label={t("exposeAPIInteraction.type")}
          name="expose_api_type"
          rules={[{ required: true, message: t("common.placeholderInput") }]}
        >
          <Select
            placeholder={t("common.placeholderSelect")}
            options={exposeAPIType}
          />
        </Form.Item>

        <Form.Item
          label={t("exposeAPIInteraction.interactionType")}
          name="interaction_type"
          rules={[{ required: true, message: t("common.placeholderInput") }]}
        >
          <Select
            placeholder={t("common.placeholderSelect")}
            options={interactionTypes}
            onChange={loadObjectInteractionDetails}
          />
        </Form.Item>

        <Form.Item
          label="Authentication type"
          name="authenticationType"
          rules={[
            {
              required: true,
              message: "Please select authentication type!",
            },
          ]}
        >
          <Select
            placeholder="Select authentication type"
            allowClear
            options={AuthenticationType}
            onChange={(e) => onSelectAuthenType(e)}
          ></Select>
        </Form.Item>

        {typeAuthen === "basic" && (
          <>
            <Form.Item
              label="User name"
              name="username"
              rules={[{ required: true, message: "Please input user name!" }]}
            >
              <Input autocomplete="new-password" />
            </Form.Item>

            <Form.Item
              label="Password"
              name="password"
              rules={[{ required: true, message: "Please input password!" }]}
            >
              <Input.Password autocomplete="new-password" />
            </Form.Item>
          </>
        )}

        <Form.Item
          label={t("exposeAPIInteraction.assignTo")}
          name="owner"
          rules={[{ required: true, message: t("common.placeholderInput") }]}
        >
          <Input placeholder={t("common.placeholderInput")} />
        </Form.Item>

        <Form.Item label="Description" name="description">
          <Input placeholder={t("common.placeholderInput")} />
        </Form.Item>

        <Form.Item
          label={t("exposeAPIInteraction.fieldReturn")}
          name="field_return"
          rules={[{ required: true, message: t("common.placeholderInput") }]}
        >
          <Select
            placeholder={t("common.placeholderSelect")}
            options={_.get(objectFields, "obj_interaction_00000001", [])}
          />
        </Form.Item>

        <Form.Item label="" name="is_batch_action" valuePropName="checked">
          <Checkbox disabled={!_.isEmpty(detailsExposeAPI)}>
            {t("exposeAPIInteraction.batchInsert")}
          </Checkbox>
        </Form.Item>

        <Keys>
          <legend>Interaction</legend>

          <AddKey onClick={_onAddKeyInteraction}>
            + {t("exposeAPIInteraction.addKey")}
          </AddKey>

          <div>
            {interactionKeys.map((item, idx) => (
              <Key key={idx}>
                <Select
                  style={{ flex: 1 }}
                  placeholder={t("common.placeholderSelect")}
                  options={_.get(
                    objectFields,
                    "obj_interaction_00000001",
                    []
                  ).filter(
                    (field) =>
                      field.type === "text" ||
                      field.type === "select" ||
                      field.type === "number" ||
                      field.type === "date" ||
                      field.type === "datetime-local"
                  )}
                  onChange={(e) => handleKeyInteraction(e, idx, "key")}
                  value={item.key}
                />
                <Input
                  style={{ flex: 1 }}
                  onChange={(e) =>
                    handleKeyInteraction(e.target.value, idx, "value")
                  }
                  value={item.value}
                />
                <Delete>
                  <Tooltip title="Delete">
                    <img
                      src={DeleteIcon}
                      onClick={() => _onDeleteKeyInteraction(idx)}
                      alt="delete"
                    />
                  </Tooltip>
                </Delete>
              </Key>
            ))}
          </div>
        </Keys>

        <Keys>
          <legend>Interaction details</legend>
          <AddKey onClick={_onAddKeyInteractionDetails}>
            + {t("exposeAPIInteraction.addKey")}
          </AddKey>

          <div>
            {interactionDetailsKeys.map((item, idx) => (
              <Key key={idx}>
                <Select
                  style={{ flex: 1 }}
                  placeholder={t("common.placeholderSelect")}
                  options={_.get(
                    objectFields,
                    interactionType === "call"
                      ? "obj_call_interaction_detail_00000001"
                      : interactionType === "email"
                      ? "obj_email_interaction_detail_00000001"
                      : interactionType === "ic"
                      ? "obj_ic_interaction_detail_00000001"
                      : "",
                    []
                  ).filter(
                    (field) =>
                      field.type === "text" ||
                      field.type === "select" ||
                      field.type === "number" ||
                      field.type === "date" ||
                      field.type === "datetime-local"
                  )}
                  onChange={(e) => handleKeyInteractionDetails(e, idx, "key")}
                  value={item.key}
                />
                <Input
                  style={{ flex: 1 }}
                  onChange={(e) =>
                    handleKeyInteractionDetails(e.target.value, idx, "value")
                  }
                  value={item.value}
                />
                <Delete>
                  <Tooltip title="Delete">
                    <img
                      src={DeleteIcon}
                      onClick={() => _onDeleteKeyInteractionDetails(idx)}
                      alt="delete"
                    />
                  </Tooltip>
                </Delete>
              </Key>
            ))}
          </div>
        </Keys>

        <div
          style={{
            border: "1px solid #ececec",
            borderRadius: "5px",
            padding: "24px 24px 0 24px",
            marginBottom: "24px",
          }}
        >
          <Form.Item
            label={t("exposeAPIInteraction.matchContactBy")}
            name="object_contact_setting_query_key"
          >
            <Select
              placeholder={t("common.placeholderSelect")}
              options={_.get(objectFields, "obj_contact_00000001", [])}
              optionFilterProp="label"
              showSearch
            />
          </Form.Item>

          <Form.Item
            label={t("exposeAPIInteraction.matchWith")}
            name="object_contact_setting_match_with"
          >
            <Input placeholder={t("common.placeholderInput")} />
          </Form.Item>

          <Form.Item
            label=""
            name="object_contact_setting_upsert"
            valuePropName="checked"
          >
            <Checkbox onChange={(e) => _onCreateNewRecord(e, "contact")}>
              {t("exposeAPIInteraction.createNewContact")}
            </Checkbox>
          </Form.Item>

          {createNewRecord.contact && (
            <div>
              <Form.Item
                label={t("workflow.addField")}
                name="object_contact_setting_addField"
              >
                <Select
                  placeholder={t("common.placeholderSelect")}
                  options={_.get(
                    objectFields,
                    "obj_contact_00000001",
                    []
                  ).filter((field) => !field.required)}
                  optionFilterProp="label"
                  showSearch
                  mode="multiple"
                  onChange={(_, options) =>
                    handleAddFieldsCreate(options, "contact")
                  }
                />
              </Form.Item>

              {mappingContact.requiredFields.map((item, idx) => (
                <Row key={idx}>
                  <Col span={8} style={{ marginBottom: "24px" }}>
                    {item.label}
                  </Col>

                  <Col span={16}>
                    {item.type === "linkingobject" ? (
                      <Select
                        options={_.get(allFields, "obj_contact_00000001", [])}
                        placeholder={t("common.placeholderSelect")}
                        style={{ width: "100%" }}
                        onChange={(e) => {
                          handleChangeFieldCreate(e, idx, true, "contact");
                        }}
                        value={item.value}
                      />
                    ) : (
                      <Input
                        onChange={(e) =>
                          handleChangeFieldCreate(
                            e.target.value,
                            idx,
                            true,
                            "contact"
                          )
                        }
                        value={item.value}
                      />
                    )}
                  </Col>
                </Row>
              ))}
              {mappingContact.optionalFields.map((item, idx) => (
                <Row key={idx}>
                  <Col span={8} style={{ marginBottom: "24px" }}>
                    {item.label}
                  </Col>

                  <Col span={16}>
                    {item.type === "linkingobject" ? (
                      <Select
                        options={_.get(allFields, "obj_contact_00000001", [])}
                        placeholder={t("common.placeholderSelect")}
                        style={{ width: "100%" }}
                        onChange={(e) => {
                          handleChangeFieldCreate(e, idx, false, "contact");
                        }}
                        value={item.value}
                      />
                    ) : (
                      <Input
                        onChange={(e) =>
                          handleChangeFieldCreate(
                            e.target.value,
                            idx,
                            false,
                            "contact"
                          )
                        }
                        value={item.value}
                      />
                    )}
                  </Col>
                </Row>
              ))}
            </div>
          )}
        </div>

        <div
          style={{
            border: "1px solid #ececec",
            borderRadius: "5px",
            padding: "24px 24px 0 24px",
            marginBottom: "24px",
          }}
        >
          <Form.Item
            label={t("exposeAPIInteraction.matchTicketBy")}
            name="object_ticket_setting_query_key"
          >
            <Select
              placeholder={t("common.placeholderSelect")}
              options={_.get(objectFields, "obj_ticket_00000001", [])}
              optionFilterProp="label"
              showSearch
            />
          </Form.Item>

          <Form.Item
            label={t("exposeAPIInteraction.matchWith")}
            name="object_ticket_setting_match_with"
          >
            <Input placeholder={t("common.placeholderInput")} />
          </Form.Item>

          <Form.Item
            label=""
            name="object_ticket_setting_upsert"
            valuePropName="checked"
          >
            <Checkbox onChange={(e) => _onCreateNewRecord(e, "ticket")}>
              {t("exposeAPIInteraction.createNewTicket")}
            </Checkbox>
          </Form.Item>

          {createNewRecord.ticket && (
            <div>
              <Form.Item
                label={t("workflow.addField")}
                name="object_ticket_setting_addField"
              >
                <Select
                  placeholder={t("common.placeholderSelect")}
                  options={_.get(
                    objectFields,
                    "obj_ticket_00000001",
                    []
                  ).filter((field) => !field.required)}
                  optionFilterProp="label"
                  showSearch
                  mode="multiple"
                  onChange={(_, options) =>
                    handleAddFieldsCreate(options, "ticket")
                  }
                />
              </Form.Item>

              {mappingTicket.requiredFields.map((item, idx) => (
                <Row key={idx}>
                  <Col span={8} style={{ marginBottom: "24px" }}>
                    {item.label}
                  </Col>

                  <Col span={16}>
                    {item.type === "linkingobject" ? (
                      <Select
                        options={_.get(allFields, "obj_ticket_00000001", [])}
                        placeholder={t("common.placeholderSelect")}
                        style={{ width: "100%" }}
                        onChange={(e) => {
                          handleChangeFieldCreate(e, idx, true, "ticket");
                        }}
                        value={item.value}
                      />
                    ) : (
                      <Input
                        onChange={(e) =>
                          handleChangeFieldCreate(
                            e.target.value,
                            idx,
                            true,
                            "ticket"
                          )
                        }
                        value={item.value}
                      />
                    )}
                  </Col>
                </Row>
              ))}
              {mappingTicket.optionalFields.map((item, idx) => (
                <Row key={idx}>
                  <Col span={8} style={{ marginBottom: "24px" }}>
                    {item.label}
                  </Col>

                  <Col span={16}>
                    {item.type === "linkingobject" ? (
                      <Select
                        options={_.get(allFields, "obj_ticket_00000001", [])}
                        placeholder={t("common.placeholderSelect")}
                        style={{ width: "100%" }}
                        onChange={(e) => {
                          handleChangeFieldCreate(e, idx, false, "ticket");
                        }}
                        value={item.value}
                      />
                    ) : (
                      <Input
                        onChange={(e) =>
                          handleChangeFieldCreate(
                            e.target.value,
                            idx,
                            false,
                            "ticket"
                          )
                        }
                        value={item.value}
                      />
                    )}
                  </Col>
                </Row>
              ))}
            </div>
          )}
        </div>

        <div
          style={{
            border: "1px solid #ececec",
            borderRadius: "5px",
            padding: "24px 24px 0 24px",
            marginBottom: "24px",
          }}
        >
          <Form.Item
            label={t("exposeAPIInteraction.matchLeadBy")}
            name="object_lead_setting_query_key"
          >
            <Select
              placeholder={t("common.placeholderSelect")}
              options={_.get(objectFields, "obj_lead_00000001", [])}
              optionFilterProp="label"
              showSearch
            />
          </Form.Item>

          <Form.Item
            label={t("exposeAPIInteraction.matchWith")}
            name="object_lead_setting_match_with"
          >
            <Input placeholder={t("common.placeholderInput")} />
          </Form.Item>

          <Form.Item
            label=""
            name="object_lead_setting_upsert"
            valuePropName="checked"
          >
            <Checkbox onChange={(e) => _onCreateNewRecord(e, "lead")}>
              {t("exposeAPIInteraction.createNewLead")}
            </Checkbox>
          </Form.Item>

          {createNewRecord.lead && (
            <div>
              <Form.Item
                label={t("workflow.addField")}
                name="object_lead_setting_addField"
              >
                <Select
                  placeholder={t("common.placeholderSelect")}
                  options={_.get(objectFields, "obj_lead_00000001", []).filter(
                    (field) => !field.required
                  )}
                  optionFilterProp="label"
                  showSearch
                  mode="multiple"
                  onChange={(_, options) =>
                    handleAddFieldsCreate(options, "lead")
                  }
                />
              </Form.Item>

              {mappingLead.requiredFields.map((item, idx) => (
                <Row key={idx}>
                  <Col span={8} style={{ marginBottom: "24px" }}>
                    {item.label}
                  </Col>

                  <Col span={16}>
                    {item.type === "linkingobject" ? (
                      <Select
                        options={_.get(allFields, "obj_lead_00000001", [])}
                        placeholder={t("common.placeholderSelect")}
                        style={{ width: "100%" }}
                        onChange={(e) => {
                          handleChangeFieldCreate(e, idx, true, "lead");
                        }}
                        value={item.value}
                      />
                    ) : (
                      <Input
                        onChange={(e) =>
                          handleChangeFieldCreate(
                            e.target.value,
                            idx,
                            true,
                            "lead"
                          )
                        }
                        value={item.value}
                      />
                    )}
                  </Col>
                </Row>
              ))}
              {mappingLead.optionalFields.map((item, idx) => (
                <Row key={idx}>
                  <Col span={8} style={{ marginBottom: "24px" }}>
                    {item.label}
                  </Col>

                  <Col span={16}>
                    {item.type === "linkingobject" ? (
                      <Select
                        options={_.get(allFields, "obj_lead_00000001", [])}
                        placeholder={t("common.placeholderSelect")}
                        style={{ width: "100%" }}
                        onChange={(e) => {
                          handleChangeFieldCreate(e, idx, false, "lead");
                        }}
                        value={item.value}
                      />
                    ) : (
                      <Input
                        onChange={(e) =>
                          handleChangeFieldCreate(
                            e.target.value,
                            idx,
                            false,
                            "lead"
                          )
                        }
                        value={item.value}
                      />
                    )}
                  </Col>
                </Row>
              ))}
            </div>
          )}
        </div>

        <WrapButton label=" ">
          <Button type="primary" htmlType="submit">
            {t("common.save")}
          </Button>
          <Button onClick={_onCancel}>{t("common.cancel")}</Button>
        </WrapButton>
      </Form>
    </ModalCustom>
  );
}

